import React from 'react';
import './SearchedList.scss';
import Movie from './Movie';

const SearchedList = props => {
  // We get props.featuredMovies (an array with the 20 featured movies) passed down from the App component

  const renderedList = props.featuredMovies.map((featuredMovie) => {
    return (
      <div>
        <Movie featuredMovie={featuredMovie} />
      </div>
    )
  });

  return (
    <div className="searched-container">
      <div className="container section-no-bottom">
        <div className="searched-container__grid u-padding-y-4">{renderedList}</div>
      </div>
    </div>
  )
}

export default SearchedList;