import React from 'react';
import './FeaturedList.scss';
import Movie from './Movie'

const FeaturedList = props => {
  // We get props.featuredMovies (an array with the 20 featured movies) passed down from the App component

  const renderedList = props.featuredMovies.map((featuredMovie) => {
    return (
      <div>
        <Movie featuredMovie={featuredMovie} />
      </div>
    )
  });

  return (
    <div className="movie-container">
      <div className="container section-no-bottom">
        <div className="movie-container__header">
          <h2 className="u-text-center text--dark">Latest Movies</h2>
          <p className="u-text-center text--dark"><i class="fas fa-arrow-left"></i> Scroll to view more of the latest movies <i class="fas fa-arrow-right"></i></p>
        </div>
        <div className="movie-container__grid u-padding-y-4">{renderedList}</div>
      </div>
    </div>
  )
}

export default FeaturedList;