import React from 'react';
import './SearchBar.scss';

class SearchBar extends React.Component {
  state = { term: '' }

  onSubmit(e) {
    e.preventDefault();

    console.log(this.state.term);

    this.props.onSubmit(this.state.term);
  }

  render() {
    return (
      <div className="search-bar u-margin-top-2">
        <div className="container section">
          <form className="search-bar__form" onSubmit={(e) => this.onSubmit(e)}>
            <div className="search-bar__inner-div">
              <input className="search-bar__input" type="text" placeholder="Search movies" value={this.state.term} onChange={(e) => this.setState({term: e.target.value})}/>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default SearchBar;