import React from 'react';
import './Movie.scss';

const IMG_API = "https://image.tmdb.org/t/p/w1280";

const Movie = props => {

  return (
    <div className="movie">
      <div className="movie__image">
        <img src={props.featuredMovie.poster_path ? IMG_API + props.featuredMovie.poster_path : "https://standrewpress.hymnsam.co.uk/images/products/large/noimage.png?width=400"} alt={props.featuredMovie.title}></img>
      </div>
      <div className="movie__caption">
        <div className="movie__title">
          {props.featuredMovie.title}
        </div>
        <div className="movie__rating">
          {props.featuredMovie.vote_average}
        </div>
      </div>
      <div className="movie__overview">
        <h3 className="u-margin-bottom-1">Overview:</h3>
        {props.featuredMovie.overview}
      </div>
    </div>
  )
}

export default Movie;